import React from "react";

import "./Amazon-Best-Selling-Book.css";
import { Books } from "./books";
import Book from "./Book";
//Variables

const Booklist = () => {
  return (
    <section className="booklist">
      {Books.map((book) => {
        return <Book id={book.id} bookData={book}></Book>;
      })}
    </section>
  );
};

export default Booklist;
