import React, { useState } from "react";

const Book = (props) => {
  const { imageLink, bookTitle, bookAuthor, quantity } = props.bookData;
  return (
    <article className="book">
      <img src={imageLink} alt="Book" />
      <h1>{bookTitle}</h1>
      <p>{bookAuthor}</p>
      <p>Quantity: {quantity}</p>
    </article>
  );
};

export default Book;
